.people-avatar-wrapper:before, .people-avatar-wrapper:after {
  opacity: 0;
}

.people-avatar-wrapper:before {
  transform: translate3d(100%, 0, 0);
}

.people-avatar-wrapper:after {
  transform: translate3d(-100%, 0, 0);
}

.people-content {
